@import url(https://fonts.googleapis.com/css?family=Audiowide);

.header {
  background-color: whitesmoke;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
  padding: 0;
}

.display {
  margin: 0 auto;
  position: relative;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.display .title {
  background: none;
  border: none;
  outline: none;
  font-family: "Audiowide", cursive;
  font-size: 2rem;
  padding: 0px 15px;
  color: #111;
  text-align: center;
  text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.2);
  padding: 0 2rem;
}
